ausing damage to the civil rights of the user and others, the legal liability arising therefrom shall be borne.\n\n2.2 The user has rights and assumes responsibility for the actions of the account held after login.\n\n2.3 If the user finds that his account or password has been illegally used by others or has abnormal use, he shall promptly notify TECH SOUTH WEST LIMITED according to the processing announced by TECH SOUTH WEST LIMITED and have the right to inform TECH SOUTH WEST LIMITED to take measures to suspend the login and use of the account.\n\n2.4 TECH SOUTH WEST LIMITED shall take measures to suspend the login and use of the user account according to the user's notice. TECH SOUTH WEST LIMITED shall require the user to provide and verify the personal valid identity information consistent with the registered identity information.\n\n2.4.1 TECH SOUTH WEST LIMITED verifies that the personal valid identity information provided by the user is consistent with the registered identity information, and shall take measures to suspend the login and use of the user account in a timely manner.\n\n2.4.2 TECH SOUTH WEST LIMITED violates the agreement of 2.4.1. If the measures are not taken in time to suspend the login and use of the user account, and therefore cause losses to the user, they shall bear the corresponding legal responsibilities.\n\n2.4.3 TECH SOUTH WEST LIMITED has the right to refuse the user's request if the user does not provide his or her personal valid ID or if the personal valid ID provided by the user is inconsistent with the registered identity information.\n\n2.5 In order to maintain the legitimate rights and interests of the user and provide TECH SOUTH WEST LIMITED with personal valid identity information consistent with the registered identity information, BobWalker shall provide the user with the necessary assistance and support for the account registrant certificate, original registration information, etc., as needed. Provide relevant evidence and 